@charset "UTF-8";
.regalia {
  margin-top: 200px;
  width: 100%;
}
.regalia .regaliaList {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}
.regalia .regaliaList p {
  position: relative;
  box-sizing: border-box;
  width: 25%;
  padding-right: 100px;
  margin-bottom: 100px;
}
.regalia .regaliaList p:nth-child(4) {
  padding-left: 10px;
  padding-right: 10px;
}
.regalia .regaliaList p:nth-child(5n+1) .circle {
  height: 30px;
  width: 30px;
}
.regalia .regaliaList p:nth-child(4n+1) .circle {
  top: 50%;
  right: 30%;
}
.regalia .regaliaList p:nth-child(3n+3) .circle {
  bottom: 100%;
  right: 30%;
}

.circle {
  position: absolute;
  display: block;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-color: #514A7E;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
}

h2 {
  font-weight: 300;
  font-size: 40px;
  line-height: 34.92px;
  margin-bottom: 100px;
  position: relative;
}

.stockHeader:after {
  content: "Акциинеакции";
  position: absolute;
  top: 80%;
  left: 25%;
  font-family: "Bad Script", cursive;
  color: #514A7E;
  font-weight: 400;
  font-size: 40px;
  line-height: 35px;
}

.stock {
  width: 100%;
}
.stock .stockItems {
  width: 1304px;
}

