.item {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-right: 30px;
  margin-bottom: 50px;
  width: 280px;
}
.item .imgItem {
  margin-bottom: 25px;
  width: 100%;
  height: 320px;
  overflow: hidden;
}
.item .imgItem img {
  width: 100%;
}
.item .itemPrice {
  margin-bottom: 10px;
}
.item .itemPrice span {
  font-size: 14px;
  line-height: 16.4px;
  color: #121212;
  margin-right: 30px;
  font-weight: 400;
}
.item .itemPrice .oldPrice {
  text-decoration: line-through;
  color: #7D7D7D;
  font-weight: 300;
}
.item .itemBody {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
  width: 100%;
}
.item .itemBody .description {
  font-size: 12px;
  height: 50px;
  flex: 0.9;
  overflow: hidden;
}
.item .itemFooter {
  display: flex;
  width: 100%;
  justify-content: space-between;
}
.item .itemFooter span {
  display: flex;
}
.item .itemFooter span img {
  margin-left: 5px;
  width: 9px;
  height: 9px;
}

.btnMore {
  width: 148px;
  height: 37px;
  background: none;
  border: 1px solid #121212;
}
.btnMore .arrowBtn {
  display: flex;
  align-items: center;
  margin-left: 30px;
}
.btnMore .arrowBtn .triangleRight {
  width: 0;
  height: 0;
  border-top: 7px solid transparent;
  border-bottom: 7px solid transparent;
  border-left: 10px solid #514A7E;
}
.btnMore .arrowBtn .line {
  width: 70px;
  border-bottom: 1px solid #514A7E;
}
.btnMore:hover {
  color: white;
  cursor: pointer;
  background-color: #514a7e;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
}
.btnMore:hover .line {
  border-bottom: 1px solid white;
}
.btnMore:hover .triangleRight {
  border-left: 10px solid white;
}

.btnMorePage {
  width: 132px;
  height: 132px;
  border-radius: 100%;
  background-color: transparent;
  border: 1px solid #514A7E;
  color: #514A7E;
  position: absolute;
  bottom: -115px;
  left: 45%;
}
.btnMorePage:hover {
  background: #514a7e;
  color: #fffdf5;
  cursor: pointer;
}

